@import '@dpe/base/src/global';
@import 'bootstrap/scss/button-group';
@import 'bootstrap/scss/buttons';

// Buttons
//
// Package: `@dpe/buttons`
//
// See [Bootstrap buttons](https://getbootstrap.com/docs/4.1/components/buttons/) for usage.
// Some styles have been overridden as demonstrated below.
//
// Style guide: 4.button

.btn {
  position: relative;

  &:hover,
  &:focus {
    top: -1px;
  }

  &:hover {
    text-decoration: underline;
  }
}

// Primary button
//
// :hover - Hover
// :focus - Focus
//
// Markup: primary-button.twig
//
// Style guide: 4.button.primary

.btn-primary,
.btn-outline-primary {
  &:hover,
  &:active {
    background-color: lighten($link-color, 20%);
    border-color: lighten($link-color, 20%);
  }
}

// Outline button
//
// :hover - Hover
// :focus - Focus
//
// Markup: outline-button.twig
//
// Style guide: 4.button.outline

.btn-outline-primary,
.btn-outline-secondary,
.btn-outline-light {
  &.btn-sm {
    border-width: 1px;
  }
}

// Link button
//
// Styled to match the custom [link](/components-library/dist/styleguide/section-2.html#kssref-2-link)
//
// :hover - Hover
// :focus - Focus
//
// Markup: link-button.twig
//
// Style guide: 4.button.link

.btn-link {
  padding: 0 0 1px;
  border-radius: 0;
  transition: color .2s ease-in-out;
  border-width: 0 0 $border-width;
  vertical-align: middle;

  &:hover,
  &:focus,
  &.focus-visible {
    color: $link-hover-color;
    padding-bottom: 0;
    border-bottom-width: 2px;
    border-color: inherit;
  }
}

// Button groups
//
// See [Bootstrap button groups](https://getbootstrap.com/docs/4.1/components/button-group/) for documentation.
//
// Markup: button-group.twig
//
// Style guide: 4.button.groups

// Icon integration
//
// Extend the Bootstrap buttons with our icon library. Simply add the
// `.btn-icon-[left|right]` class along with any [icon](/components-library/dist/styleguide/section-3.html#kssref-3-default)
// class from the available set.
//
// **Make sure you have the `@dpe/icons` package installed and are including it's styles.**
//
// .btn-icon-left - The left sided icon: `btn-icon-left`
// .btn-icon-right - The right sided icon: `btn-icon-right`
//
// Markup: icon-button.twig
//
// Style guide: 4.button.icon

.btn-icon,
.btn-icon-left {
  padding-left: $spacer * 2.5;
  background-position: left $spacer-m center;

  &.btn-sm {
    padding-left: $spacer-l;
    background-position: left $spacer-s center;
  }

  &.btn-lg {
    padding-left: $spacer * 2.75;
    background-position: left $spacer-m center;
  }
}

.btn-icon-right {
  padding-right: $spacer * 2.5;
  background-position: right $spacer-m center;

  &.btn-sm {
    padding-right: $spacer-l;
    background-position: right $spacer-s center;
  }

  &.btn-lg {
    padding-right: $spacer * 2.75;
    background-position: right $spacer-m center;
  }
}

// Light means white.
.btn-light {
  background-color: $background;
  border-color: $background;
}
